.lm-player-message-mask {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  background: transparent;
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: all 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  pointer-events: none;
}
.lm-player-message-mask.lm-player-mask-loading-animation {
  opacity: 1;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}
.lm-player-message-mask .lm-player-loading-icon {
  position: relative;
  font-size: 48px;
  z-index: -1;
  visibility: hidden;
}
.lm-player-message-mask .lm-player-loading-icon.lm-player-loading-animation {
  visibility: visible;
  z-index: 9;
  animation: loadingAnimation 1s linear infinite;
}
.lm-player-message-mask .lm-player-loading-icon.lm-player-loadfail {
  visibility: visible;
  z-index: 9;
}
.lm-player-message-mask .lm-player-message {
  color: #fff;
  font-size: 16px;
  padding: 10px;
}
@keyframes loadingAnimation {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
